import React, { Component } from 'react'
import bannerIllustrator from "../../images/banner-illustration.svg";

class Banner extends Component {
	componentDidMount() {
		window.addEventListener("scroll", this.toggleBodyClass);
		this.toggleBodyClass();
	  }

	  componentWillUnmount() {
		window.removeEventListener("scroll", this.toggleBodyClass);
	  }

	  toggleBodyClass = () => {
		if (window.scrollY < 40) {
		  document.body.classList.remove("stickyHeader");
		} else {
		  document.body.classList.add("stickyHeader");
		}
	};


	render() {
		const bannerHeading = "InfoSoft Global (P) Ltd."
		const bannerInfo = "Incorporated in 2005, our journey started in Kolkata as a software product company with FusionCharts as our first product, which received global acclaim with over 28,000 customers, and since then we’ve been growing and evolving every day."

		return (
			<div className="banner py-8 md:py-16" >
				<div className="container mx-auto px-8 md:px-16 pt-8 pb-16 md:py-16 ">
					<div className="flex flex-col md:flex-row justify-center items-center">
						<div className="max-w-2xl  md:mr-10">
							<h1 className="text-3xl lg:text-4xl my-8 text-gray-800">{ bannerHeading }</h1>
							<p className="text-gray-700">{ bannerInfo }</p>
						</div>
						<div className="flex-grow mt-8"><img className="mx-auto mb-4" src={bannerIllustrator} alt=""/></div>
					</div>
				</div>
			</div>
		)
	}
}

export default Banner



